.wrapper {
  display: flex;
  height: 150px;
  justify-content: center;
  gap: 70px;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: row;
}

.button {
  padding: 15px 25px;
  font-size: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: rgb(7, 7, 255);
  background-color: none;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px 20px rgb(0, 0, 255);
  animation: test 10s infinite;
}

@keyframes test {
  0% {
    box-shadow: 0 9px 20px #9103f7;
    color: #9103f7;
  }
  25% {
    box-shadow: 0 9px 20px rgb(0, 251, 255);
    color: rgb(0, 251, 255);
  }
  50% {
    box-shadow: 0 9px 20px rgb(125, 99, 255);
    color: rgb(125, 99, 255);
  }
  100% {
    box-shadow: 0 9px 20px rgb(0, 94, 255);
    color: rgb(0, 94, 255);
  }
}

.button:hover {
  background-color: rgb(20, 20, 36);
}

.button:active {
  background-color: none;
  border-image-slice: 1;
  box-shadow: 0 5px rgb(255, 255, 255);
  transform: translateY(4px);
}
