.fab {
  color: rgb(0, 72, 255);
  font-size: 25pt;
  position: relative;
  text-shadow: 0 9px 20px rgb(0, 0, 255);
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;

}
@-webkit-keyframes example {
  0%   {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
  10%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  20% {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
  30%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  40% {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
  50%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  60%   {color:rgb(216, 216, 216); bottom:0px;}
  70%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  80% {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
  90%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  100% {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
}
@keyframes example {
  0%   {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
  10%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  20% {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
  30%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  40% {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
  50%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  60%   {color:rgb(216, 216, 216); bottom:0px;}
  70%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  80% {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
  90%  {color: rgb(0, 72, 255);text-shadow: 0 9px 20px rgb(0, 0, 255); bottom: 5px;}
  100% {color:rgb(216, 216, 216); text-shadow: 0 9px 20px rgb(216, 216, 216); bottom:0px;}
}
