.footer {
  margin: auto;
  margin-top: 25px;
  position: relative;
  width: 340px;
  border: 1px solid rgb(0, 0, 255);
  padding: 5px;
  border-radius: 20px;
}
.switch {
  position: relative;
  right: 140px;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* .switch input { 
    opacity: 0;
