  } */

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 1px solid rgb(0, 0, 255);
  background-color: rgb(8, 8, 14);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: rgb(254, 254, 254);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: rgb(7, 7, 255);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 15px;
}

.slider.round:before {
  border-radius: 50%;
}

.display {
  font-family: "Dancing script", monospace;
  display: inline-block;
  right: 10px;
  position: absolute;
  text-align: right;
  font-size: 20px;
  margin: 0;
  margin-top: 4px;
  color: rgb(52, 52, 92);
}
